<template lang="pug">
.Makeresume
    .makeresume
        .topBar  {{step}}-{{substep}}
            .skipButton(@click="skipBtn") skip 
        #makeReOptionbar
            .optionDraw
                .firstOption.optionDot(:class="step>1?'opFinColor':'opyetFinColor'")
                .firstOption.optionLine(:class="step>1?'opFinColor':'opyetFinColor'")
                .otherOption.optionDot(:class="step>2?'opFinColor':'opyetFinColor'")
                .otherOption.optionLine(:class="step>2?'opFinColor':'opyetFinColor'")
                .otherOption.optionDot(:class="step>3?'opFinColor':'opyetFinColor'")
                .otherOption.optionLine(:class="step>3?'opFinColor':'opyetFinColor'")
                .otherOption.optionDot(:class="step>4?'opFinColor':'opyetFinColor'")
            .optionText
                .firstOption.mkReoption(:class="step>1?'rpFinColor':'rpYetColor'")
                    div 
                .otherOption.mkReoption(:class="step>2?'rpFinColor':'rpYetColor'")
                    div 
                .otherOption.mkReoption(:class="step>3?'rpFinColor':'rpYetColor'")
                    div 
                .otherOption.mkReoption(:class="step>4?'rpFinColor':'rpYetColor'")
                    div 
        .chatAndInput
            .chat
                .robot(style="background-image:url(https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/robot-2@2x.png)")
                .sayAndTest
                    .robotSay(v-if="botsay") {{botsay.que}}
                    chaTest(v-if="chaTestStatus" v-on:finshTest="changeTestStatue") 
            
            #ResumeBlock
                input.mkRebotinput(v-model="resumeInput" placeholder="  SAY..." @keyup.enter="checkAns")
                button.mkRebotinputBtn(@click="checkAns" style="background-image:url(https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/arrow_upward-24px.png)")
</template>

<script>
import { db } from '@/components/fireFile.js';
import chaTest from '@/components/CharacterTest.vue';
const fStore = db.firestore();
export default {
    components: {chaTest},
    data() {
        return{
            options: [
                {name: "",status: false,content: ""},
                {name: "",status: false,content: ""},
                {name: "",status: false,content: ""},
                {name: "",status: false,content: ""}
            ],
            resumeData: {
                name: '',
                school: '',
                skills: [],
                projects: [],
                awards: [],
                charitristic: ""
            },
            resumeStatus: false,
            chaTestStatus: false,
            charitristic: "",
            resumeInput: "",
            step: 1,
            substep: 1,
            botsay: {},
            botsayTemp: [],
            allAns: {}
        }
    },
    mounted(){
        this.$bind('botsay',fStore.collection('BotSentence').doc('r1'));
        this.$bind('allAns',fStore.collection('BotAnswer'));
    },
    watch:{
        'botsayTemp': function(temp){
            let bots = temp[0];
            if(bots){
                bots.que = bots.que.replace("name",this.resumeData.name);
                bots.que = bots.que.replace("school",this.resumeData.school);
                this.botsay = bots;
            }
        }
    },
    methods: {
        changeTestStatue: function(cha){
            this.chaTestStatus = false;
            this.charitristic=cha;
            this.resumeData.charitristic = cha;
            this.botsay.que = ""+this.resumeData.name+""+cha+"";
            this.step=4;
            this.substep=3;
        },
        addAns: function(){
            if(this.botsay.sel){
                if(this.botsay.sel!='skills'&&this.botsay.sel!='projects'&&this.botsay.sel!='awards'){
                    this.resumeData[this.botsay.sel] = this.resumeInput;
                }
                else{
                    this.resumeData[this.botsay.sel].push(this.resumeInput);
                }
            }
        },
        skipBtn: function(){
            if(this.step==5){
                this.$router.push({ path: 'myresume' })
            }
            this.substep=1;
            this.step+=1;
            this.checkAns();
        },
        addResume: function () {
            fStore.collection('Resume').add({
                'name': this.resumeData.name,
                'school': this.resumeData.school,
                'skills': this.resumeData.skills,
                'projects': this.resumeData.projects,
                'awards': this.resumeData.awards,
                'charitristic': this.resumeData.charitristic
            })
            .then(() => {
                this.$router.push({ path: 'myresume?user='+this.resumeData.name });
            });
        },
        checkAns: function(){
            this.addAns();
            var botInput = this.resumeInput;
            function isCherries(fruit) { 
                return fruit.sel == botInput;
            }
            let theSel = this.allAns.find(isCherries);
            if(theSel){
                this.$bind('botsayTemp',fStore.collection('BotSentence').where("id", "==",theSel.que));
            }
            else{
                if(this.step==4 && this.substep==2){
                    this.chaTestStatus = true;
                }
                else if(this.step==4 && this.substep==3){
                    if(this.charitristic==""){
                        this.$bind('botsayTemp',fStore.collection('BotSentence').where("id", "==", "tiger"));
                    }
                    else if(this.charitristic==""){
                        this.$bind('botsayTemp',fStore.collection('BotSentence').where("id", "==", "bird"));
                    }
                    else if(this.charitristic==""){
                        this.$bind('botsayTemp',fStore.collection('BotSentence').where("id", "==", "bear"));
                    }
                    else if(this.charitristic==""){
                        this.$bind('botsayTemp',fStore.collection('BotSentence').where("id", "==", "owl"));
                    }
                    this.step=5;
                    return 0;
                }
                else if(this.step==5){
                    this.addResume();
                }
                else if(this.substep==1){
                    this.substep+=1;
                }
                else{
                    this.substep=1;
                    this.step+=1;
                }
                this.$bind('botsayTemp',fStore.collection('BotSentence').where("id", "==", "r"+this.step+"-"+this.substep));
            }
            this.resumeInput= "";
        },
        answer: function(id){
            this.$bind('botsayTemp',fStore.collection('BotSentence').where("id", "==", id))
        }
    }
}
</script>

