<template lang="pug">
.Myresume
    .resume
        .resumeTop 
        .resumeContent
            .photoAndName
                .resumePhoto
                .resumeName {{resumeData.name}}
                .resumeSchool {{resumeData.school}}
            .ability
                .resumeDetail
                    .resumeTitle 
                    .resumeText(v-for="i in resumeData.skills" v-if="i!=''") {{i}}
                .resumeDetail
                    .resumeTitle 
                    .resumeText(v-for="i in resumeData.projects") {{i}}
                .resumeDetail
                    .resumeTitle 
                    .resumeText(v-for="i in resumeData.awards") {{i}}
        .resumeButton
            .resumeBtn.resumeEdit(@click="changePage('/makeresume')") 
            .resumeBtn.resumeConfirm(@click="changePage(finishPath)") 
</template>

<script>
import { db } from "@/components/fireFile.js";
const fStore = db.firestore();
export default {
    data() {
        return{
            resumeData: {
                name: '',
                school: '',
                skills: ['Photoshop','Premiere',''],
                projects: ["www.bangandcool.com ","2019 ","2018 "],
                awards: [" "," "]
            },
            resumeDataTemp: {},
            finishPath: '/charesume'
        }
    },
    mounted() {
        if(this.$route.query.user){
            this.$bind('resumeDataTemp',fStore.collection('Resume').where("name", "==", this.$route.query.user)).then(() => {
                this.resumeData = this.resumeDataTemp[0];
                if(this.resumeData.charitristic)
                    this.finishPath = '/charesume?user='+this.$route.query.user+'&charitristic='+this.resumeData.charitristic;
                else
                    this.finishPath = '/charesume?user='+this.$route.query.user;
            });
        }
    },
    methods: {
        changePage: function(path){
            this.$router.push(path);
        }
    }

};
</script>

