<template lang="pug">
.chaTest
    .chaTestList(v-if="queStep<chatrstiseTest.length")
        div(v-for="t in chatrstiseTest[queStep]" @click="chaTests(t)" ) 
            button.chaTestBtn(:class="classNames[ansList.indexOf(t.type)]") {{t.name}}
    button.chaTestBtn(v-if="queStep<chatrstiseTest.length" @click="nextQue") OK
    .chaTestText {{chaTestBot}}
</template>

<script>

export default {
    data() {
        return{
            queStep: 0,
            chatrstiseTest: [
                [{name: "",type: "D"},
                {name: "",type: "I"},
                {name: "",type: "S"},
                {name: "",type: "C"}],
                [{name: "",type: "D"},
                {name: "",type: "I"},
                {name: "",type: "S"},
                {name: "",type: "C"}],
                [{name: "",type: "D"},
                {name: "",type: "I"},
                {name: "",type: "S"},
                {name: "",type: "C"}]
            ],
            classNames: ['chaTest1','chaTest2','chaTest3','chaTest4'],
            chaTestBot: "",
            ansList: [],
            chaList: [{name: "",score: 0},{name: "",score: 0},{name: "",score: 0},{name: "",score: 0}]
        }
    },
    methods: {
        nextQue: function(){
            if(this.ansList.length<4){
                this.chaTestBot = "";
                return 0;
            }
            this.chaTestBot = "";
            for(let i=0;i<4;i++){
                let chaType = this.ansList[i];
                let score=7-2*i;
                if(chaType=='D'){
                    this.chaList[0].score+=score;
                }
                if(chaType=='I'){
                    this.chaList[1].score+=score;
                }
                if(chaType=='S'){
                    this.chaList[2].score+=score;
                }
                if(chaType=='C'){
                    this.chaList[3].score+=score;
                }
            }
            this.ansList=[];
            this.queStep+=1;
            
            if(this.queStep==this.chatrstiseTest.length){
                this.chaList.sort(function (a, b) {
                    return b.score - a.score;
                });
                let cha = this.chaList[0].name;
                this.$emit('finshTest',cha);
            }
        },
        chaTests: function(t){
            var findAns = this.ansList.indexOf(t.type);
            
            if(findAns==-1){
                this.ansList.push(t.type);
            }
            else{
                this.ansList.splice(findAns, 1);
            }
        }
    }
}
</script>

