    background-color: #fcfbf7 !important
.chaTestList
    width: 500px
    display: flex
    flex-wrap: wrap
    flex-direction: row
    justify-content: space-between
    margin-bottom: 20px
.chaTestBtn
    width: 110px
    border-radius: 18px
    background-color: #fff
    color: $c-secondary
    padding: 7px 10px
    font-size: $smaller-font-size
    border: 0px
    outline: none
    font-family: monospace,'Noto Sans TC'
    cursor: pointer
@media only screen and (max-width: 480px)
    .chaTestList
        width: 100%
        margin: auto
        justify-content: space-around
    .chaTestBtn
        margin-bottom: 10px
</style>