<template lang="pug">
.Share
    .SlideShow
        .mySlides.fade
            .numbertext
            img(src="https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/slideshow/slideshow1.png" style="width:100%")

        .mySlides.fade
            .numbertext
            img(src="https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/slideshow/slideshow2.png" style="width:100%")

        .mySlides.fade
            .numbertext
            img(src="https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/slideshow/slideshow3.png" style="width:100%")

        a.prev(@click='plusSlides(-1)' style="background-image:url(https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/arrow_back_ios-24px.svg)") 
        a.next(@click='plusSlides(1)' style="background-image:url(https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/arrow_forward_ios-24px.svg)") 
        
    .outcomeTab
        .outcomeType 
        .outcomeType 
        .outcomeType 
    .outcome
        .outcomeContainer(v-for="(outcome,i) in outcomes" @click="goInto")
            .outcomeImg(:style="{'background-image': 'url(' + outcome.src + ')'}")
            .outcomeText
                .outcomeTitle {{outcome.title}}
                .outcomeLikes
                    .likeIcon
                    .likeNum {{outcome.like}}
                .outcomeAuthor {{outcome.text}}   
</template>
<script>
var slideIndex = 1;
export default {
  data() {
    return {
        slideIndex: 1,
        outcomes: [
            {src: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/outcome/365.png',title: '365',text: '',url:'/marble',like: 10,view: 5,type: 1},
            {src: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/outcome/Adobe.png',title: 'Adobe',text: '',url:'/myFriend',like: 10,view: 5,type: 1},
            {src: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/outcome/.png',title: '',text: '',url: '/lightArt',like: 10,view: 5,type: 1},
            {src: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/outcome/.png',title: '',text: '',url: '/lightArt',like: 10,view: 5,type: 1},
            {src: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/outcome/.png',title: '',text: '',url: '/lightArt',like: 10,view: 5,type: 2},
            {src: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/outcome/.png',title: '',text: '',url: '/lightArt',like: 10,view: 5,type: 2},
            {src: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/outcome/.jpg',title: '',text: '',url: '/lightArt',like: 10,view: 5,type: 2},
            {src: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/outcome/.png',title: '',text: '',url: '/lightArt',like: 10,view: 5,type: 2}
        ]
    }
    },
    mounted(){
        this.showSlides(slideIndex);
    },
    methods: {
        plusSlides(n) {
            this.showSlides(slideIndex += n);
        },
        currentSlide(n){
            this.showSlides(slideIndex = n);
        },
        showSlides(n) {
            var i;
            let shaSlides = document.getElementsByClassName("mySlides");
            
            if(shaSlides[0]){
                // var dots = document.getElementsByClassName("dot");
                if (n > shaSlides.length) {slideIndex = 1}    
                if (n < 1) {slideIndex = shaSlides.length}
                for (i = 0; i < shaSlides.length; i++) {
                    shaSlides[i].style.display = "none";  
                }
                
                shaSlides[slideIndex-1].style.display = "block";  
            }
            
        },
        goInto: function(){
            this.$router.push({path:'/sharepage'});
        }
  }
}
</script>
