<template lang="pug">
#Talkroom
    .createBox
        input.botinput(v-model="queid" placeholder="id")
        input.botinput(v-model="que" placeholder="")
        input.botinput(v-model="quesel" placeholder="id()" @keyup.enter="addMessage")
        button.botinputBtn(@click="addMessage") 
    .createBox
        input.botinput(v-model="selid" placeholder="id")
        input.botinput(v-model="sel" placeholder="")
        input.botinput(v-model="selque" placeholder="id()" @keyup.enter="addSelector")
        button.botinputBtn(@click="addSelector") 
    .createBox
        input.botinput(v-for="ho in home" v-model="ho.value" :placeholder='ho.name')
        button.botinputBtn(@click="addHome") 
</template>

<script>
import { db } from "@/components/fireFile.js";
const fStore = db.firestore();
export default {
    data() {
        return{
            que: "",
            queid: "",
            quesel: "",
            sel: "",
            selid: "",
            selque: "",
            home: [
                {name: "src",value: "https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/home/7fabfa239ddb434a933a578b1785b2d2-1.png"},
                {name: "deadline",value: "2020,7,26,8:00:10"},
                {name: "title",value: ""},
                {name: "text",value: ""},
                {name: "view",value: ""},
                {name: "group",value: ""},
            ]
        }
    },
    methods: {
        addMessage: function () {
            let sels = this.quesel.split(",");
            fStore.collection('BotSentence').add({
                'sel': sels,
                'id': this.queid,
                'que': this.que
            })
            .then(() => {
                this.quesel="";
                this.que="";
                this.queid="";
            });
        },
        addSelector: function () {
            fStore.collection('BotAnswer').add({
                'sel': this.sel,
                'id': this.selid,
                'que': this.selque
            })
            .then(() => {
                this.sel="";
                this.selid="";
                this.selque="";
                // console.log("add suc");
            });
        },
        addHome: function(){
            fStore.collection('Home').add({
                'src': this.home[0].value,
                'deadline': this.home[1].value,
                'title': this.home[2].value,
                'text': this.home[3].value,
                'view': this.home[4].value,
                'group': this.home[5].value,
                'leftDay': 0,
                'leftHour': 0,
                'leftMin': 0,
                'url': ""
            })
            .then(() => {
                alert("");
            });
        }
    }
}
</script>

<style lang="sass">
.createBox
    height: 100px
    padding: 40px
.botinput
    width: 100%
    height: 20px
</style>
