<template lang="pug">
#Talkroom
    .voteTitle (5)
    .voteContainer
        .voteBlock(v-for="m in members" :class="m.type=='add'?'memberSel':'memberNoSel'" @click="choseMember(m)")
            img.voteImg(:src="m.src")
            .voteName {{m.name}}
    .voteDeBtn(@click="toChatWork()") 
</template>

<script>

export default {
    components: {
        
    },
    data() {
        return{
            members: [
                {name: "",src: "https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//face1.png",type: "add"},
                {name: "",src: "https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//face2.png",type: "add"},
                {name: "",src: "https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//face3.png",type: "no"},
                {name: "",src: "https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//face4.png",type: "np"},
                {name: "",src: "https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//face10.png",type: "add"},
                {name: "",src: "https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//face1-1.png",type: "no"},
                {name: "",src: "https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//face1-2.png",type: "no"},
                {name: "",src: "https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//face1-3.png",type: "no"},
                {name: "",src: "https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//face1-4.png",type: "no"}
            ]
        }
    },
    methods:{
        choseMember: function(m){
            if(m.type!='add'){
                m.type='add';
            }
            else{
                m.type='no';
            }
        },
        toChatWork: function(){
            this.$router.push("/workroom");
        }
    }
};
</script>

