<template lang="pug">
.makeresume
    .topBar 
    .chabotHead(:class='chaImage')
    .sayAndTest
        .robotSay.charobotSay {{name}} 
    .chaAccBtn(@click="changePage()") 
</template>

<script>

export default {
    components: {
        
    },
    mounted(){
        if(this.$route.query.user)
            this.name = this.$route.query.user;
        if(this.$route.query.charitristic){
            this.charitristic = this.$route.query.charitristic;
            if(this.charitristic==''){
                this.chaImage = 'tiger';
            }
            if(this.charitristic==''){
                this.chaImage = 'peacock';
            }
            if(this.charitristic==''){
                this.chaImage = 'owl';
            }
        }
    },
    data() {
        return{
            name: '',
            charitristic: '',
            chaImage: 'koala'
        }
    },
    methods: {
        changePage: function(){
            if(this.$route.query.user){
                this.$router.push('/?user='+this.$route.query.user);
            }
            else{
                this.$router.push('/');
            }
        }
    }
};
</script>

