<template lang="pug">
.chatroom
    #chatTopbar 10
    #chatTopSign 
    #chatBack(@click="backHome()")
    .chatTabBar
        .chatroomTab(@click='changeChatTab1()') 
        .chatroomTab(@click='changeChatTab2()') 
        .chatroomTab(@click='changeChatTab3()') 
    #Optionbar
        .option(v-for='(menu,i) in options' :key="i" :class="i==optionIdx ?'opIsActive': 'opDied'")
            a#chatOption(v-if="i==0" :href='menu.link') {{menu.name}}
            router-link#chatOption(v-if="i!=0" :to='menu.link') {{menu.name}}
            #chatOptionEnter(v-if="i==0" @click='changeChatTab2()')
            router-link#chatOptionEnter(v-if="i!=0" :to='menu.link' @click='changeChatTab2()')
        #opMemberTitle 
        .opMemberContainer
            .opMenBlock(v-for="m in members")
                img.opmemberImg(:src="m.src")
                .opmemberName {{m.name}}
            .memberInputBlock
                .mbAddBtn
                .mbTakePoBtn
                .mbTaleSmBtn
                input.menberInput(placeholder="SAY...")
                .mbinputBtn
    #Chatbody
        transition(name="page" mode="out-in")
            router-view
    #Botroom
        .selfnoteShado(:style="{'height': (noteStatus ? '320px':'100px')}")
            .selfnote
                .closeNoteBtn(@click="noteStatus=!noteStatus")
                .selNoteText(v-for="note in noteText") {{note}}
        .botHead
        .botVolumnbtn(:class="volumnStatus?'botOpen':'botClose'" @click="changeVolumnStatus()")
        .botState(v-if="volumnStatus")
            .botsayFixBlock
                .botsay(v-if="botsay") {{botsay.que}}
                .selectboxes(v-if="selectors" v-for="(s,i) in selectors" @click="answer(s.que)") 
                    .btnTit(v-if="selectors.length>1") {{i+1}}.
                        .btnTxt {{s.sel}}
                    .btnTxt(v-if="selectors.length==1") {{s.sel}}
            input.botinput(v-model="botInput" placeholder="SAY..." @keyup.enter="checkAns")
            .botinputBtn(@click="checkAns")
</template>

<script>
import { db } from '@/components/fireFile.js';
const fStore = db.firestore();
var botCount;
export default {
    components: {
    },
    data() {
        return {
            optionIdx: 0,
            options: [
                {name: "",link: "/chat"},
                {name: "",link: "/info"},
                {name: "",link: "/note"},
                {name: "",link: "/photo"},
                {name: "",link: "/member"},
                {name: "",link: "/vote"}
            ],
            botsay: "",
            botsayTemp: [],
            selectors: [],
            queStep: 1,
            headQueList: [1,2,3],
            botInput: "",
            allAns: {},
            noteStatus: true,
            noteText: ["19:00 "],
            members: [
                {name: "",src: "https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//face2.png",type: "add"},
                {name: "",src: "https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//face3.png",type: "no"},
                {name: "",src: "https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//face4.png",type: "np"},
                {name: "",src: "https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//face10.png",type: "add"}
            ],
            volumnStatus: true
        }
    },
    mounted(){
        this.$bind('botsay',fStore.collection('BotSentence').doc(String(this.queStep)));
        this.$bind('allAns',fStore.collection('BotAnswer'));
    },
    watch:{
        '$route': function(route){
            if(route.path=='/chat'){
                this.optionIdx = 0;
            }
            if(route.path=='/info'){
                this.optionIdx = 1;
            }
            if(route.path=='/note'){
                this.optionIdx = 2;
            }
            if(route.path=='/photo'){
                this.optionIdx = 3;
            }
            if(route.path=='/member'){
                this.optionIdx = 4;
            }
            if(route.path=='/vote'){
                this.optionIdx = 5;
            }
        },
        'botsay': function(){
            if(this.botsay){
                let sels = this.botsay.sel.split(",");
                this.$bind('selectors',fStore.collection('BotAnswer').where("id", "in", sels));
            }
        },
        'botsayTemp': function(temp){
            this.botsay = temp[0];
        },
        'selectors': function(sel){
            if(!sel){
                this.$bind('selectors',fStore.collection('BotAnswer').doc("1"));
            }
        }
    },
    created(){
        window.scroll({
            top: 0,
            left: 0,
            behavior: 'smooth'
        });
        botCount = setTimeout(() => this.plusQuestion(), 30000);
    },
    beforeDestroy(){
        clearTimeout(botCount);
    },
    methods: {
        plusQuestion: function(){
            if(this.queStep<this.headQueList.length){
                this.queStep+=1;
            }
            else{
                this.queStep=1;
            }
            this.$bind('botsay',fStore.collection('BotSentence').doc(String(this.queStep)));
        },
        changeVolumnStatus: function(){
            if(this.volumnStatus){
                this.volumnStatus = false;
                clearTimeout(botCount);
            }
            else{
                this.volumnStatus = true;
                botCount = setTimeout(() => this.plusQuestion(), 30000);
                if(this.queStep==1){
                    this.queStep=3;
                }
                else{
                    this.queStep=1;
                }
                this.$bind('botsay',fStore.collection('BotSentence').doc(String(this.queStep)));
            }
        },
        backHome: function(){
            this.$router.push('/chatList');
        },
        checkAns: function(){
            var botInput = this.botInput;
            if(botInput==1||botInput==2||botInput==3){
                botInput = this.selectors[botInput-1].sel;
            }
            function isCherries(fruit) { 
                return fruit.sel == botInput;
            }
            let theSel = this.allAns.find(isCherries);
            
            if(theSel){
                this.$bind('botsayTemp',fStore.collection('BotSentence')
                    .where("id", "==", theSel.que))
                    .then(() => {
                        this.botInput = "";
                });
            }
            else{
                this.$bind('botsay',fStore.collection('BotSentence').doc('1'))
            }
            clearTimeout(botCount);
            botCount = setTimeout(() => this.plusQuestion(), 30000);
        },
        answer: function(id){
            this.$bind('botsayTemp',fStore.collection('BotSentence').where("id", "==", id));
            clearTimeout(botCount);
            botCount = setTimeout(() => this.plusQuestion(), 30000);
        },
        changeChatTab1(){
            document.getElementById("Chatbody").style.display = "none";
            document.getElementById("Botroom").style.display = "none";
            document.getElementById("Optionbar").style.display = "block";
        },
        changeChatTab2(){
            document.getElementById("Chatbody").style.display = "block";
            document.getElementById("Botroom").style.display = "none";
            document.getElementById("Optionbar").style.display = "none";
        },changeChatTab3(){
            document.getElementById("Chatbody").style.display = "none";
            document.getElementById("Botroom").style.display = "block";
            document.getElementById("Optionbar").style.display = "none";
        }
    }
}
</script>

