<template lang="pug">
#app
  #nav
    router-link(to="/").iconDoo Dooperation
    router-link(v-for='(menu,i) in menuList' :key='i' :to='menu.url') {{menu.name}}
    .searchIcon(@click="dosearch")
    input.searchInput(v-if="searchStatus" v-model="searchQuery" @keyup.enter="checkSearch")
    .personIcon(@click="makeResume")
    .personText HI! {{username}}
  .navSpace
  transition(name="page" mode="out-in")
    router-view

</template>

<script>

export default {
  name: "App",
  components: {
    
  },
  data() {
    return{
      menuList: [
        {name:'',url:'/'},
        {name:'',url:'/chatlist'},
        {name:'',url:'/share'}
      ],
      searchStatus: false,
      searchQuery: "",
      username: ""
    }
  },
  created(){
    if(this.$route.query.user){
      this.username = this.$route.query.user;
    }
  },
  watch: {
    '$route.query': function(query){
      if(query.user){
        this.username = query.user;
      }
    }
  },
  methods:{
    dosearch: function(){
      this.searchStatus = !this.searchStatus;
    },
    checkSearch: function(){
      this.$router.push({path: '/',query: {search: this.searchQuery}});
    },
    makeResume: function(){
      this.$router.push({path: '/makeresume'});
    }
  }
};
</script>

