<template lang="pug">
.SharePage
    .outcomeIntro
        .articleImg(:style="{'background-image': 'url(' + img + ')'}")
        .articleText
            .articleTitle {{title}}
            .articleDate {{date}}
            .PageText
                div 
                div {{intro}}
    .outcomeProcess
        .PageTitle 
        .processImg
            .processPic.processPic1(:style="{'background-image': 'url(' + processpic1 + ')'}")
            .processPic.processPic2(:style="{'background-image': 'url(' + processpic2 + ')'}")
            .processPic.processPic3(:style="{'background-image': 'url(' + processpic3 + ')'}")
        .PageText {{processtext}}
    .outcomeSTAR
        .PageTitle S.T.A.R 
        .PageSubtitle Situation/Task 
        .PageText {{st}}
        .PageSubtitle Analyze
        .PageText {{a}}
        .PageSubtitle Result
        .PageText {{r}}
    .outcomeMember
        .PageTitle 
        .members
            .memberList(v-for="i in member")
                .memberImg(:style="{'background-image': 'url(' + i.src + ')'}")
                .PageText.memberName {{i.name}}
    .outcomeMessage
        .PageTitle 
        .Message(v-for="(i,id) in message")
            .messagePhoto(:style="{'background-image': 'url(' + photo + ')'}")
            .messageText
                .messageName {{name}}
                .messageDate {{time}}
                .messageContent {{message[id]}}
        input#messageInput(v-model="enteringMessage" @keyup.enter="addOn(enteringMessage)" placeholder="...")
    .tool
        .toolIcon#tool1(:style="{'background-image': 'url(' + tool1 + ')'}" @click="toMessage()")
        .toolIcon#tool2(:style="{'background-image': 'url(' + tool2 + ')'}" @click="changeLike()")
        .toolIcon#tool3(:style="{'background-image': 'url(' + tool3 + ')'}" @click="changeMark()")
        .toolIcon#tool4(:style="{'background-image': 'url(' + tool4 + ')'}")
            .visited {{visited}}
</template>
<script>
var likePressed=0;
var markPressed=0;
var messageId=0;
export default {
    created(){
        window.scroll({
            top: 0,
            left: 0,
            behavior: 'smooth'
        });
    },
    data(){
        return{
            member: [
                {src:"https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//member1.png",name:""},
                {src:"https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//member2.png",name:""},
                {src:"https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//member3.png",name:""},
                {src:"https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//member4.png",name:""},
            ],
            tool1:"https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/chat_bubble-24px.svg",
            tool2:"https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/favorite-24px.svg",
            tool3:"https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/bookmark-24px.svg",
            tool4:"https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/ic_visibility_24px.svg",
            visited:"104",
            message: [],
            enteringMessage:"",
            photo:"https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//member5.png",
            name:"",
            time:"2020/06/05 13:19",
            img: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//intro.png',
            title: '',
            date: '2020/05/06 17:32',
            intro: '47,627    ',
            processtext: ' ',
            processpic1: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//process1.png',
            processpic2: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//process2.png',
            processpic3: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//process3.png',
            member1: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//member1.png',
            member2: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//member2.png',
            member3: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//member3.png',
            member4: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//member4.png',
            member5: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets//member5.png',
            st: '   ',
            a: '  ',
            r: '   2.0 '

        }
    },
    methods: {
        addOn(t){     
            this.message.push(t);
            messageId++;
            console.log(messageId);
            this.enteringMessage="";
            // document.getElementById("messageInput").value = "";
        },
        changeLike(){
            if(likePressed==0){
                document.getElementById("tool2").style.backgroundImage="url(https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/favorite-24px-red.png)";
                document.getElementById("tool2").style.filter="contrast(100%)";
                likePressed=1;
            }
            else{
                document.getElementById("tool2").style.backgroundImage="url(https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/favorite-24px.svg)";
                document.getElementById("tool2").style.filter="contrast(10%)";
                likePressed=0;
            }
        },
        changeMark(){
            if(markPressed==0){
                document.getElementById("tool3").style.backgroundImage="url(https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/bookmark-24px-yellow.png)";
                document.getElementById("tool3").style.filter="contrast(100%)";
                markPressed=1;
            }
            else{
                document.getElementById("tool3").style.backgroundImage="url(https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/bookmark-24px.svg)";
                document.getElementById("tool3").style.filter="contrast(10%)";
                markPressed=0;
            }
        },
        toMessage(){
            window.scroll({
                top: 3500,
                left: 0,
                behavior: 'smooth'
            });
        }
    }
}
</script>
