import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Chatroom from "../views/Chatroom.vue"
import Workroom from "../views/Workroom.vue"
import ChatList from "../views/ChatList.vue"
import Chat from "../views/chatroom/Chat.vue"
import Info from "../views/chatroom/Info.vue"
import Member from "../views/chatroom/Member.vue"
import Note from "../views/chatroom/Note.vue"
import Photo from "../views/chatroom/Photo.vue"
import Vote from "../views/chatroom/Vote.vue"
import CreateBot from "../views/chatroom/createBot.vue"
import MakeResume from "../views/MakeResume.vue"
import MyResume from "../views/MyResume.vue"
import ChaResume from "../views/ChaResume.vue"
import ProjectPage from "../views/project.vue"
import Share from "../views/Share.vue"
import SharePage from "../views/SharePage.vue"

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/chatlist",
    component: ChatList
  },
  {
    path: "/chatroom",
    name: "Chat",
    component: Chatroom,
    children: [
      {path: "/chat",component: Chat},
      {path: "/info",component: Info},
      {path: "/member",component: Member},
      {path: "/note",component: Note},
      {path: "/photo",component: Photo},
      {path: "/vote",component: Vote},
      {path: "/createbot",component: CreateBot}
    ]
  },
  {
    path: "/workroom",
    component: Workroom,
    children: [
      {path: "chat",component: Chat},
      {path: "info",component: Info},
      {path: "member",component: Member},
      {path: "note",component: Note},
      {path: "photo",component: Photo},
      {path: "vote",component: Vote},
      {path: "createbot",component: CreateBot}
    ]
  },
  {
    path: "/myresume",
    component: MyResume
  },
  {
    path: "/charesume",
    component: ChaResume
  },
  {
    path: "/makeresume",
    component: MakeResume
  },
  {
    path: "/project",
    component: ProjectPage
  },
  {
    path: "/share",
    component: Share
  },
  {
    path: "/sharepage",
    component: SharePage
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
