<template lang="pug">
#Talkroom
    .countBar
        .counticon
        p {{parseInt(leftDay)}}{{parseInt(leftHour)}}{{parseInt(leftMin)}}
    .chatContainer
        .chatBlock(v-for="msg in messages")
            .otherUserBlock
                .chatName(v-if="msg.name!=name" :class="colorList[setColor(msg.name)]") {{msg.name.substring(0, 1)}}
                .chatText(v-if="msg.name!=name") {{msg.message}}
                .chatTime(v-if="msg.name!=name") {{countTimeToRead(msg.time)}}
            .chatSelfText(v-if="msg.name==name") {{msg.message}}
            .chatSpaceBlock(v-if="msg.name==name")
        #chatScroll
    .memberInputBlock
        .mbAddBtn
        .mbTakePoBtn
        .mbTaleSmBtn
        input.menberInput(placeholder="SAY..." v-model="message" @keyup.enter=" addMessage")
        .mbinputBtn(@click="addMessage")
</template>

<script>
import { db } from "@/components/fireFile.js";
const fStore = db.firestore();
export default {
    data() {
        return{
            leftDay: 0,
            leftHour: 0,
            leftMin: 0,
            message: "",
            name: "",
            messages: [],
            colorList: ['bgLightPurple','bgLightPink','bgLightBlue','bgLightYellow','bgLightGreen']
        }
    },
    created() {
        this.countTime();
        if(this.$route.query.user){
            this.name = this.$route.query.user;
        }
    },
    mounted() {
        this.$bind('messages',fStore.collection('Message').orderBy('time')).then(()=>{
            var element = document.getElementById("chatScroll");
            element.scrollIntoView({block: "end"});
        });
    },
    updated(){
        
    },
    methods:{
        setColor: function(user){
            if(user=='jerry'){
                return(2);
            }
            else if(user=='emily'){
                return(1);
            }
            else if(user=='heidi'){
                return(4);
            }
            else{
                return(3);
            }
        },
        addMessage: function () {
            if(this.message!=''){
                let today = new Date();
                fStore.collection('Message').add({
                    'message': this.message,
                    'name': this.name,
                    'time': today
                })
                .then(() => {
                    this.message = "";
                    var element = document.getElementById("chatScroll");
                    element.scrollIntoView({block: "end"});
                });
            }
            
        },
        countTimeToRead: function (sec){
            let Hour = sec.toDate().getHours();
            let Min = sec.toDate().getMinutes();
            return Hour+" : "+Min;
        },
        countTime: function(){
            let today = new Date();
            let dl = new Date("2020,8,5,20:40:10");
            let lfSec = dl-today;
            let leftDay = lfSec / 1000 / 60 / 60 / 24;
            let leftHour = lfSec / 1000 / 60 / 60 % 24;
            let leftMin = lfSec / 1000 / 60 % 60;
            this.leftDay = leftDay;
            this.leftHour = leftHour;
            this.leftMin = leftMin;
        }
    }
};
</script>

